html {
  position: relative;
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  height: 100%;
  background: radial-gradient(ellipse at top, #333, #131313);
}

.core {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-family: Open Sans, sans-serif;
  color: white;

  #headshot {
    display: block;
    margin: 0 auto 25px;
  }
}

@import 'responsive';
@import 'social-links';
@import 'sponsors-employers';
